*,
body {
  margin: 0;
  padding: 0;
}

* {
  box-sizing: border-box;
}

body {
  background-color: #fafafa;
}

html,
body,
.container {
  display: block;
  justify-content: center;
}

.loader {
  width: 500px;
  height: 1060px;
  margin: 0 auto;
  display: flex;
  justify-content: center;
  align-items: center;
}

.loader .loader__text {
  font-size: 23px;
  font-weight: 600;
}

.books {
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  flex-wrap: wrap;
  padding: 70px 50px;
  width: 1400px;
}

.books .book {
  background-color: white;
  display: flex;
  align-items: flex-start;
  width: 580px;
  margin-bottom: 50px;
  padding: 20px;
  border-radius: 5px;
  color: #9e9e9e;
  box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
}

.book img {
  position: relative;
  margin-right: 20px;
  box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
}

.book .book__data {
  flex: auto;
}

.book .book__title {
  font-size: 16px;
  font-weight: 600;
  margin-bottom: 3px;
  color: #212121;
}

.book .book__author {
  margin-bottom: 10px;
}

span {
  color: #212121;
  font-weight: 600;
  margin-right: 15px;
}

p {
  margin-bottom: 5px;
}

.book .book__buy {
  margin-top: 15px;
  background-color: #b3e5fc;
  color: #212121;
  text-align: center;
  padding: 10px;
  border-radius: 6px;
}

a {
  text-decoration: none;
}
